import React, { useState, useEffect } from "react";
import Header from "../components/Header";
import "./../styles/Statistics.css";
import { ToastContainer, toast } from "react-toastify";
import DeleteConfirm from "../components/DeleteReport";
import EditReport from "../components/EditReport";
import Hero from "../components/Hero";

function extractId(report) {
  if (!report) return null;
  if (report._id && typeof report._id === "string") return report._id;
  if (report._id && report._id.$oid) return report._id.$oid;
  if (report._id && report._id.toString) return report._id.toString();
  if (report.id) return report.id;
  return null;
}

function AdminHistory() {
  const [incidentes, setIncidentes] = useState([]); // Lista completa sin filtrar
  const [filteredIncidents, setFilteredIncidents] = useState([]); // Lista que se muestra
  const violenceTypes = [
    "Physical Violence",
    "Psychological Violence",
    "Sexual Violence",
    "Workplace Violence",
    "Discrimination",
  ];

  const zoneOptions = [
  "El Viejo",
  "La Playita",
  "El Jaguar",
  "Farmacia",
  "Quimica",
  "Medicina",
  "Veterinaria",
  "Enfermeria",
  "Derecho",
  "Humanas",
  "Freud",
  "Ondontologia y Ciencias Humanas",
  "Diseo Grafico",
  "Entrada de la 26",
  "Capilla",
  "Museo de Arte y Parque",
  "Museo de Arquitectura",
  "Plaza Che",
  "Parque Entrada 30",
  "Administrativos Calle 30",
  "Economia y Arquitectura",
  "Musica y Artes",
  "EM e Hidraulica",
  "Laboratorios de Ingenierias",
  "Aulas de Ingenieria",
  "CYT",
  "Ciencias",
  "Humbolt",
  "Parque CYT",
  "Complejo Deportivo",
  "IPARM",
  "Biologia",
  "Jardin UN",
  "Posgrados de Humanas y Geologia",
  "Edificio Gloria Galeano y Agronomia",
  "ICA",
  "Invernaderos",
  "Hemeroteca",
  "Administrativos"
];

  const path = "https://cacviun-backend.onrender.com";

  // ==========================================
  // ESTADOS DE FILTROS
  // ==========================================
  const [filters, setFilters] = useState({
    user_email: "",
    ageMin: "",
    ageMax: "",
    category: "",
    zone: "",
    startDate: "",
    endDate: "",
  });

  // Manejador genrico para los cambios en los filtros
  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters((prev) => ({ ...prev, [name]: value }));
  };
  
  // ==========================================
  // FETCH Y APLICACIN INICIAL DE FILTROS
  // ==========================================
  const fetchHistory = async () => {
    try {
      const res = await fetch(`${path}/report/admin-history`);

      if (!res.ok) {
        console.error("Error when checking history");
        return;
      }

      const data = await res.json();
      const allReports = data.reportHistory || [];
      setIncidentes(allReports);
      setFilteredIncidents(allReports); // Inicialmente, la lista filtrada es la lista completa

    } catch (error) {
      console.error("Error connecting to server", error);
    }
  };

  useEffect(() => {
    fetchHistory();
  }, []);
  
  // ==========================================
  // FUNCIN PRINCIPAL DE FILTRADO
  // Se ejecuta cada vez que cambian los filtros o la lista de incidentes.
  // ==========================================
  useEffect(() => {
    const applyFilters = () => {
      let tempIncidents = incidentes;

      // 1. FILTRO por user_email (Admin Only)
      if (filters.user_email) {
        tempIncidents = tempIncidents.filter((inc) =>
          inc.user_email.toLowerCase().includes(filters.user_email.toLowerCase())
        );
      }

      // 2. FILTRO por category
      if (filters.category) {
        tempIncidents = tempIncidents.filter((inc) =>
          inc.category.toLowerCase().includes(filters.category.toLowerCase())
        );
      }
      
      // 3. FILTRO por zone
      if (filters.zone) {
        tempIncidents = tempIncidents.filter((inc) =>
          inc.zone.toLowerCase().includes(filters.zone.toLowerCase())
        );
      }

      // 4. FILTRO por age (Rango)
      const minAge = parseInt(filters.ageMin);
      const maxAge = parseInt(filters.ageMax);
      
      if (!isNaN(minAge)) {
          tempIncidents = tempIncidents.filter((inc) => inc.age >= minAge);
      }
      if (!isNaN(maxAge)) {
          tempIncidents = tempIncidents.filter((inc) => inc.age <= maxAge);
      }
      
      // 5. FILTRO por creationTime (Rango de fechas)
      const startDate = filters.startDate ? new Date(filters.startDate) : null;
      const endDate = filters.endDate ? new Date(filters.endDate) : null;

      if (startDate || endDate) {
          tempIncidents = tempIncidents.filter(inc => {
              // Asumo que inc.date o inc.creationTime tiene la fecha legible para Date()
              const dateSource = inc.date || inc.creationTime;
              if (!dateSource) return false; // Si no hay campo de fecha, ignorar
              
              const reportDate = new Date(dateSource); 
              let isValid = true;
              
              if (startDate) {
                  // Compara solo la fecha, ignora la hora (mayor o igual)
                  isValid = isValid && reportDate >= startDate;
              }
              if (endDate) {
                  // Para incluir el da final, comparamos con el inicio del da siguiente
                  const nextDay = new Date(endDate);
                  nextDay.setDate(nextDay.getDate() + 1);
                  isValid = isValid && reportDate < nextDay;
              }
              return isValid;
          });
      }


      // Importante: Reiniciar la paginacin al aplicar un nuevo filtro
      setCurrentPage(1); 
      setFilteredIncidents(tempIncidents);
    };

    applyFilters();
  }, [filters, incidentes]); // Se ejecuta cuando cambian los filtros o la lista original


  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);

  const [selectedReport, setSelectedReport] = useState(null);

  // DELETE  abre modal
  const handleDeleteClick = (rep) => {
    setSelectedReport(rep);
    setShowDeleteModal(true);
  };

  //DELETE  ejecuta confirmacin
  const confirmDelete = async () => {
    try {
      const id = extractId(selectedReport);
      if (!id) {
        toast.error("Invalid report ID", { theme: "colored" });
        return;
      }

      const res = await fetch(`${path}/report/delete/${id}`, {
        method: "DELETE",
        headers: { "Content-Type": "application/json" },
      });

      const data = await res.json();

      if (res.ok && data.success) {
        toast.success("Report deleted successfully!", { theme: "colored" });
        await fetchHistory(); // recargar lista real
      } else {
        toast.error(data.message || "Error deleting report", {
          theme: "colored",
        });
      }
    } catch (e) {
      console.error(e);
      toast.error("Server error when deleting report", { theme: "colored" });
    }

    setShowDeleteModal(false);
  };

  // EDIT  abre modal
  const handleEditClick = (rep) => {
    setSelectedReport(rep);
    setShowEditModal(true);
  };

  // EDIT  guardar cambios
  const saveEditedReport = async (editedData) => {
    try {
      const id = extractId(editedData);
      if (!id) {
        toast.error("Invalid report ID", { theme: "colored" });
        return;
      }

      const body = {
        category: editedData.category, // string legible
        description: editedData.description,
      };

      const res = await fetch(`${path}/report/edit/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      });

      const data = await res.json();

      if (res.ok && data.success) {
        toast.success("Report updated successfully!", { theme: "colored" });
        await fetchHistory(); // refrescar datos desde backend real
      } else {
        toast.error(data.message || "Error updating report", {
          theme: "colored",
        });
      }
    } catch (error) {
      console.error(error);
      toast.error("Server error when updating report", { theme: "colored" });
    }

    setShowEditModal(false);
  };

  // ==========================================
    // PAGINACIN + ITERATOR PATTERN
    // La fuente de datos es filteredIncidents
    // ==========================================
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 15;
  
    function createIncidentIterator(collection) {
      let index = 0;
  
      return {
        next: () => {
          if (index < collection.length) {
            return { value: collection[index++], done: false };
          }
          return { done: true };
        },
      };
    }
  
    const indexOfLast = currentPage * itemsPerPage;
    const indexOfFirst = indexOfLast - itemsPerPage;
  
    const currentIncidents = filteredIncidents.slice(indexOfFirst, indexOfLast); // Usamos filteredIncidents
  
    const iterator = createIncidentIterator(currentIncidents);
  
    let visibleRows = [];
    let step = iterator.next();
    while (!step.done) {
      visibleRows.push(step.value);
      step = iterator.next();
    }
  
    const nextPage = () => {
      if (currentPage < Math.ceil(filteredIncidents.length / itemsPerPage)) {
        setCurrentPage(currentPage + 1);
      }
    };
  
    const prevPage = () => {
      if (currentPage > 1) {
        setCurrentPage(currentPage - 1);
      }
    };
  

  return (
    <div className="Statistics-container">
      <Header view="statistics" />

      <div className="Statistics-content">
        <h1
          className="Statistics-form-title"
          style={{ textAlign: "center", padding: "0.1rem" }}
        >
          Historial for Admin
        </h1>

        <p
          className="Statistics-form-subtitle"
          style={{ textAlign: "center", fontSize: "1.1rem", padding: "0.3rem" }}
        >
          In this space you can see Reports made by all users on campus. You can filter as you need.

        </p>

        {/* ========================================== */}
        {/* CONTROLES DE FILTRO */}
        {/* ========================================== */}
        <div className="filter-controls">
            
            {/* GRUPO 1: Texto y Rango de Edad */}
            <div className="filter-group">
                {/* Filtro por Email */}
                <input
                    type="text"
                    name="user_email"
                    placeholder="Filter by Email"
                    value={filters.user_email}
                    onChange={handleFilterChange}
                    className="filter-input"
                />
            </div>
            
            {/* GRUPO 2: Category y Zone (Dropdowns) */}
            <div className="filter-group">
                {/* Filtro por Category (Dropdown) */}
                <select
                    name="category"
                    value={filters.category}
                    onChange={handleFilterChange}
                    className="filter-select"
                >
                    <option value="">Filter by Category (All)</option>
                    {violenceTypes.map(type => (
                        <option key={type} value={type}>{type}</option>
                    ))}
                </select>

                {/* Filtro por Zone (Dropdown) */}
                <select
                    name="zone"
                    value={filters.zone}
                    onChange={handleFilterChange}
                    className="filter-select"
                >
                    <option value="">Filter by Zone (All)</option>
                    {zoneOptions.map(zone => (
                        <option key={zone} value={zone}>{zone}</option>
                    ))}
                </select>
            </div>
            
            {/* GRUPO 3: Rango de Edad y Fechas */}
            <div className="filter-group">
                <div className="filter-age-range">
                    <input
                        type="number"
                        name="ageMin"
                        placeholder="Min Age"
                        value={filters.ageMin}
                        onChange={handleFilterChange}
                        className="filter-input"
                    />
                    <input
                        type="number"
                        name="ageMax"
                        placeholder="Max Age"
                        value={filters.ageMax}
                        onChange={handleFilterChange}
                        className="filter-input"
                    />
                </div>
                
                <div className="filter-date-range">
                    <label>From:</label>
                    <input
                        type="date"
                        name="startDate"
                        value={filters.startDate}
                        onChange={handleFilterChange}
                        className="filter-input"
                    />
                    <label>To:</label>
                    <input
                        type="date"
                        name="endDate"
                        value={filters.endDate}
                        onChange={handleFilterChange}
                        className="filter-input"
                    />
                </div>
            </div>
            
        </div>
        
        {/* Tabla de incidentes */}
        {filteredIncidents.length > 0 ? (
          <div className="tabla-container">
            <table className="tabla-incidentes">
              <thead>
                <tr>
                  <th>Email</th>
                  <th>Age</th>
                  <th>Description</th>
                  <th>Date</th>
                  <th>Category</th>
                  <th>Zone</th>
                </tr>
              </thead>

              <tbody>
                {visibleRows.map((inc, index) => (
                  <tr key={index}>
                    <td>{inc.user_email}</td>
                    <td>{inc.age}</td>
                    <td>{inc.description}</td>
                    <td>{inc.date}</td>
                    <td>{inc.category}</td>
                    <td>{inc.zone}</td>
                    <td>
                      <button
                        className="btn-edit"
                        onClick={() => handleEditClick(inc)}
                      >
                        Edit
                      </button>

                      <button
                        className="btn-delete"
                        onClick={() => handleDeleteClick(inc)}
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            {/* PAGINATION */}
            <div className="pagination-controls">
              <button
                disabled={currentPage === 1}
                onClick={prevPage}
                className="pagination-btn"
              >
                 Previous
              </button>

              <span className="pagination-info">
                Page {currentPage} of{" "}
                {Math.ceil(filteredIncidents.length / itemsPerPage)}
              </span>

              <button
                disabled={
                  currentPage === Math.ceil(filteredIncidents.length / itemsPerPage)
                }
                onClick={nextPage}
                className="pagination-btn"
              >
                Next 
              </button>
            </div> 
          </div>
        ) : (
          <p style={{ textAlign: "center", fontSize: "0.8rem" }}>
            {/* Mensaje dinmico si no hay datos o si los filtros no coinciden */}
            {incidentes.length === 0 ? "No data available." : "No reports match the current filters."}
          </p>
        )}


      </div>

      <ToastContainer />

      {showDeleteModal && (
        <DeleteConfirm
          report={selectedReport}
          onClose={() => setShowDeleteModal(false)}
          onConfirm={confirmDelete}
        />
      )}

      {showEditModal && (
        <EditReport
          report={selectedReport}
          onClose={() => setShowEditModal(false)}
          onSave={saveEditedReport}
        />
      )}
        <Hero/>
    </div>
  );
}

export default AdminHistory;