import React, { useState } from "react";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./../styles/Report.css";
import Header from "../components/Header";
import Hero from "../components/Hero";
import { useSessionStore } from "./../session/sessionStore.ts";
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  useMapEvents,
} from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import * as turf from "@turf/turf";
const customIcon = new L.Icon({
  iconUrl: "https://cdn-icons-png.flaticon.com/512/684/684908.png",
  iconSize: [32, 32],
  iconAnchor: [16, 32],
});

const violenceTypes = [
  "Physical Violence",
  "Psychological Violence",
  "Sexual Violence",
  "Workplace Violence",
  "Discrimination",
];

function Report() {
  const session = useSessionStore((state) => state.session);
  const path = "https://cacviun-backend.onrender.com";

  const name = session?.name || "";
  const email = session?.email || "";
  const [age, setAge] = useState("");
  const [date, setDate] = useState("");
  const [type, setType] = useState("");
  const [description, setDescription] = useState("");
  

  // Estado para mostrar u ocultar la ventana emergente
  const [showConfirm, setShowConfirm] = useState(false);

  // Map / location state
  const [position, setPosition] = useState(null);
  const [zone, setZone] = useState(null);
  const zoneRef = React.useRef(null);
  const [error, setError] = useState("");

  // Latitude / longitude stored as strings
  const [latitudStr, setLatitudStr] = useState("");
  const [longitudStr, setLongitudStr] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!name.trim() || !email.trim() || !age.trim() || !date.trim() || !type.trim()) {
      toast.error("All required fields must be filled", { position: "top-center" });
      return;
    }

    // Email institucional obligatorio
    const emailRegex = /^[^\s@]+@unal\.edu\.co$/;
    if (!emailRegex.test(email)) {
      toast.error("Email must be @unal.edu.co", { position: "top-center" });
      return;
    }

    if (isNaN(age) || Number(age) <= 0) {
      toast.error("Age must be a valid positive number", { position: "top-center" });
      return;
    }

    // Validacin de fecha
    const selectedDate = new Date(date);
    const minDate = new Date("2000-01-01");
    const today = new Date();

    if (selectedDate < minDate) {
      toast.error("Date must be after the year 2000", { position: "top-center" });
      return;
    }

    if (selectedDate > today) {
      toast.error("Date must be before today", { position: "top-center" });
      return;
    }

    //  Mostrar ventana emergente de confirmacin
    setShowConfirm(true);
  };


  const confirmSubmit = async() => {
    
    try{
      const body = {
        email: email,
        date: date,
        age: age,
        type: type,
        description: description,
        location: { latitud: latitudStr, longitud: longitudStr },
        sendTime: new Date().toLocaleString("sv-SE"),
      };

      console.log(body);

      const res = await fetch(`${path}/report/save-report`,{
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      });

      if (!res.ok) {
        toast.error("Server error. Please try again.", {
          position: "top-center",
        });
        return;
      }

      const data = res.json();
      if(data.success){
        toast.success(data.message, {
          position: "top-center",
          autoClose: 2000,
        });
      }else{
        toast.error(data.message, {
          position: "top-center",
        });
      }
    }catch(error){
      console.log(error);
      toast.error("Error connecting to the server.", {
        position: "top-center",
      });
      resetForm();
    } 
    toast.success("Report submitted successfully", {
      position: "top-center",
      autoClose: 2000,
    });

    // Reset form
    resetForm();

    setShowConfirm(false);
  };

  const resetForm= () =>{
    setAge("");
    setDate("");
    setType("");
    setDescription("");
  }

  const ClickMarker = () => {
    useMapEvents({
      click(e) {
        const { lat, lng } = e.latlng;
        
        setPosition(e.latlng);
        // Store lat/lng as strings
        setLatitudStr(String(lat));
        setLongitudStr(String(lng));
      },
    });

    return position ? (
      <Marker position={position} icon={customIcon}>
        <Popup>
          Selected location
        </Popup>
      </Marker>
    ) : null;
  };

  const confirmationModal = (
    <div className="modal-background">
      <div className="modal-box">
        <h3>Confirmation Required</h3>
        <p>
          Are you completely sure that all the information entered is correct
          and you want to submit this report?
        </p>

        <div className="modal-buttons">
          <button className="yes-btn" onClick={confirmSubmit}>YES</button>
          <button className="no-btn" onClick={() => setShowConfirm(false)}>NO</button>
        </div>
      </div>
    </div>
  );

  return (
    <div className="report-container">
      <Header />

      {showConfirm && confirmationModal}

      <div className="report-content">
        <div className="form-header-text">
          <h1 className="form-title">Make your Report</h1>
          <p className="form-subtitle">Please complete the survey</p>
        </div>

        <form onSubmit={handleSubmit} className="report-form-single-column">

          <div className="form-group">
            <label htmlFor="name">Name and Last Name</label>
            <input
              type="text"
              id="name"
              value={name}
              readOnly
              className="input-disabled"
            />
          </div>

          <div className="form-group">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              value={email}
              readOnly
              className="input-disabled"
            />
          </div>

          <div className="form-group">
            <label htmlFor="age">Age</label>
            <input
              type="number"
              id="age"
              placeholder="Your age"
              value={age}
              onChange={(e) => setAge(e.target.value)}
            />
          </div>

          <div className="form-group">
            <label htmlFor="date">Date of Event</label>
            <input
              type="date"
              id="date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
            />
          </div>

          <div className="form-group">
            <label htmlFor="type">Type of Violence</label>
            <select
              id="type"
              value={type}
              onChange={(e) => setType(e.target.value)}
            >
              <option value="">Select</option>

              {violenceTypes.map((t, index) => (
                <option key={index} value={t}>
                  {t}
                </option>
              ))}
            </select>
          </div>

          <div className="form-group">
            <label htmlFor="description">Please describe the event</label>
            <textarea
              id="description"
              placeholder="Describe the event (optional)"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
            ></textarea>
          </div>

          <div className="form-group">
            <label htmlFor="map-area">
              Please select the are on the map where the events occurred.
            </label>
            <div style={{ height: "400px", width: "100%", marginTop: "1rem" }}>
              <MapContainer
                center={[4.638193, -74.084046]}
                zoom={17}
                minZoom={16}
                maxZoom={18}
                maxBounds={[
                  [4.6315, -74.0935],
                  [4.6445, -74.077],
                ]}
                maxBoundsViscosity={1.0}
                style={{ height: "100%", width: "100%" }}
              >
                <TileLayer
                  attribution="&copy; OpenStreetMap"
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                <ClickMarker />
              </MapContainer>
            </div>
          </div>

          <button type="submit" className="submit-btn">
            Send the Report
          </button>
        </form>

      </div>
        <Hero />
      <ToastContainer />
    </div>
  );
}

export default Report;
